html {
  height: 100%;
}

body {
  height: 100%;
  overflow: hidden;
}

* {
  box-sizing: border-box;
}

.appContainer {
  height: 100%;
}

.App {
  text-align: center;
  height: 100%;
  display: flex;

  .app-toolbars {
    height: 100px;
  }
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.design-selector {
  display: inline-block;
  border: 2px solid black;
  padding-left: 2px;
  padding-right: 2px;
  margin-right: 3px;
  font-size: large;

  &:hover {
    background-color: lightgreen;
  }
}

.design-selector-active {
  font-weight: bold;
  background-color: lightblue;
}

.menu {
  .menu-item {
    width: 32px;
    height: 32px;
  }

  .selected {
    background-color: lightgreen;
  }

  display: flex;
}

.menu-side {
  flex-direction: column;
}

.main-layout {
  border-collapse: collapse;
  
  tr {
    td {
      border: 1px solid gray;
      overflow: hidden;
      vertical-align: top;
    }
  }
}

.main-layout-div {
  .main-layout-row {
    display: flex;
    .main-layout-cell {
      border: 1px solid black;
      overflow: hidden;
    }
  }
}

.three-container {
}

.side-drawing-container {
  display: flex;
  flex-direction: column;

  margin-left: auto;
  margin-right: auto;
  border: 3px solid black;
  width: fit-content;
}